import React from 'react';
import SidePanel from './SidePanel';

const SidePanels = () => (
  <g id="SidePanels" transform="translate(0.000000, 6.000000)">
    <SidePanel transform="translate(0, 0)" />
    <SidePanel transform="translate(870, 0)" />
  </g>
);

export default SidePanels;



